.reviews{
  @media (min-width: @tablet-width){
    display:grid;
    grid-template-columns: 1fr 175px;
    background-image: url("../img/review_background.png");
    background-repeat: no-repeat;

  }
  margin-top: 58px;
}
.reviews__link{
  width: 100%;
  margin-top: 36px;
  margin-bottom: 45px;
  @media (min-width: @tablet-width){
    grid-column: 2/3;
    grid-row: 1/2;
    width:175px;
  }
}
.reviews__title {
  @media (min-width: @tablet-width){
    grid-column: 1/2;
    grid-row:1/2;
  }
  margin-bottom: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
}

.reviews__list {
  .no-markers-list;
  margin: 0;
}

.reviews__content {
  @media (min-width: @tablet-width){
    grid-column: 1/-1;
    grid-row: 2/-1;
    font-size: 17px;
    line-height: 30px;
  }
  margin-top: 14px;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
  color: @tundora;
}

.reviews__author {
  margin-top: 33px;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
}

.reviews__author-id {
  font-weight: 400;
  font-size: 17px;
  line-height: 30px;
  color: @tundora;
}

.reviews__wrapper {
  display:flex;
  @media (min-width: @tablet-width){
    grid-column: 2/3;
    grid-row:2/3;
    margin-top: auto;
  }
  margin-bottom: 0;
  margin-top: 30px;
}

.reviews__switch {
  background-color: @white;
  border: 0;
  padding: 0;
  &:nth-child(2){
    margin-left:37px;
  }
}
